  methods:{
   delComment:function(){
       this.$confirm('履歴を削除しまが?', 'メッセージ', {
          confirmButtonText: 'はい',
          cancelButtonText: 'いいえ',
          type: 'warning'
        }).then(() => {
          window.localStorage.clear();
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
         this.$router.go({path: this.$router.currentRoute.path, force: true})
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
   } 
  }
}
