import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedstate from 'vuex-persistedstate'
import { UPDATE_CURRENT, UPDATE_BOOK } from "../mutation-types";
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    //本情報
    books: [],
    // 現在の本情報
    current: null
  },
  getters: {
    //  レニュー数 
    booksCount(state) {
      return state.books.length;
    },
    //  すべてのレビュー情報
    allbooks(state) {
      return state.books;
    },
    //  指定ページのレビュー情報
    getRangePage(state) {
      return page => {
        const SIZE = 3;
        return state.books.slice((page - 1) * SIZE, (page - 1) * SIZE + SIZE)
      }
    },
    //  指定IDの書籍情報
    getBookById(state) {
      return id => {
        return state.books.find(book => book.id === id);
      }
    },
    // 現在編集の書籍情報
    current(state) {
      return state.current;
    }
  },
  mutations: {
    // カレント本を更新
    [UPDATE_CURRENT](state, payload) {
      state.current = payload
    },

    // 本が存在の場合更新、存在しない場合、新規作成
    [UPDATE_BOOK](state, payload) {
      // 本を取得
      let b = this.getters.getBookById(payload.id)
      if (b) {
        // 本の更新
        Object.assign(b, payload)
      } else {
        // 本の追加
        state.books.push(payload)
      }
    }
  },
  actions: {
    // 本の更新
    [UPDATE_CURRENT]({ commit }, payload) {
      commit(UPDATE_CURRENT, payload)
    },
    // 本の更新
    [UPDATE_BOOK](context, payload) {
      context.commit(UPDATE_BOOK, payload)
    }
  },
  modules: {
  },
  plugins: [createPersistedstate({
    key: 'testbook',
    storage: localStorage
  })]
})
