<template>
  <div id="home">
    <BookInfo v-for="(b,i) in books" :book="b" :index="i + 1" :linkable="true" :key="b.id"></BookInfo>
   
    <div>
      <el-pagination
        background
        :total="booksCount"
        :page-size="3"
        @current-change="onchange"
        layout="prev,pager,next"
      ></el-pagination>
    </div>
  </div>
</template>
<script>
import BookInfo from "./BookInfo";
//eslint-disable-next-line
import { mapMutations, mapGetters } from "vuex";
export default {
  name: "Home",
  data() {
    return {
      books: []
    };
  },
  components: {
    BookInfo
  },
  computed: {
    ...mapGetters(["booksCount", "getRangePage"])
  },
  methods: {
    onchange(page) {
      this.books = this.getRangePage(page);
    }
  },
  mounted:function(){
      this.books = this.getRangePage(1)
  }
};
</script>