<template>
  <div id="bookInfo" class="clearfix" :class="{ linkable }" @click="onclick">
    <div class="image">
      <img :src="book.image" />
    </div>

    <div class="details">
      <ul>
        <li v-if="index" :key="index">{{ index }}</li>
        <li :key="book.title">{{ book.title }}</li>
        <li :key="book.author">{{ book.author }}</li>
        <li :key="book.publisher">{{ book.publisher}}</li>
        <li :key="book.published">{{ book.published }}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import { UPDATE_CURRENT } from "../mutation-types";

export default {
  name: "BookInfo",
  props: {
    book: { type: Object },
    index: { type: Number },
    linkable: { type: Boolean, default: false }
  },
  methods: {
    //Vuexを呼び出す
    ...mapActions([UPDATE_CURRENT]),
    onclick: function() {
      if (this.linkable) {
        // 現在の本更新
        this[UPDATE_CURRENT](this.book);
        //コメント画面に遷移
        this.$router.push("/form");
      }
    }
  }
};
</script>

<style scoped>
.linkable:hover {
  cursor: pointer;
  background-color: #ff9;
}

.image {
  float: left;
}

.image img {
  height: 180px;
}

.details {
  float: left;
}

.details li {
  list-style-type: none;
  text-align: left;
}

.clearfix {
  width: 100%;
  margin-bottom: 10px;
}

.clearfix:after {
  content: "";
  display: block;
  clear: both;
}
</style>